<template>
	<a-card>
		<SearchForm @handleSearch="handleSearch">
			<a-row>
				<a-button type="primary" @click="onAdd"></a-button>
			</a-row>
			<a-table :columns="columns" :data-source="data" :pagination="pagination"
					 :rowKey="(row,index) => {return index}"
					 @change="handleTableChange">
				<span slot="isEnable" slot-scope="text" :style="!text ? {color:'red'} : ''">
					{{text?'':''}}
				</span>
				<span slot="locked" slot-scope="text" :style="text ? {color:'red'} : ''">
					{{text?'':''}}
				</span>
				<span slot="isAdmin" slot-scope="text" :style="text ? {color:'red'} : ''">
					{{text?'':''}}
				</span>
				<span slot="action" slot-scope="text,record">
					<a @click="onEdit(record)"></a>
					<a-divider type="vertical"/>
					<a-popconfirm
							title="?"
							ok-text=""
							cancel-text=""
							@confirm="unlock(record)"
					>
						<a></a>
					</a-popconfirm>
					<a-divider type="vertical"/>
					<a-popconfirm
							title="?"
							ok-text=""
							cancel-text=""
							@confirm="onDelete(record.id)"
					>
						<a style="color:red;"></a>
					</a-popconfirm>
				</span>
			</a-table>
		</SearchForm>
		<div>
			<a-modal v-model="visible" title="" @ok="handleOk" cancelText="" okText="">
				<a-form-model
						:rules="rules"
						ref="ruleForm"
						:label-col="{span:6}"
						:wrapper-col="{span:12}"
						:model="form">
					<a-form-model-item label="" prop="name">
						<a-input v-model="form.name"/>
					</a-form-model-item>
					<a-form-model-item label="" prop="username">
						<a-input v-model="form.username"/>
					</a-form-model-item>
					<a-form-model-item label="" prop="password">
						<a-input type="password" v-model="form.password" placeholder=""/>
					</a-form-model-item>
					<a-form-model-item label="" prop="confirm_password">
						<a-input type="password" v-model="form.confirm_password" placeholder=""/>
					</a-form-model-item>
					<a-form-model-item label="" prop="isEnable">
						<a-switch v-model="form.isEnable"/>
					</a-form-model-item>
					<a-form-model-item label="" prop="isAdmin">
						<a-switch v-model="form.isAdmin"/>
					</a-form-model-item>
				</a-form-model>
			</a-modal>
		</div>
	</a-card>
</template>
<script>
    import SearchForm from "./SearchForm";

    const columns = [
        {
            title: '',
            dataIndex: 'name',
        },
        {
            title: '',
            dataIndex: 'username',
        },
        {
            title: '',
            dataIndex: 'isAdmin',
            scopedSlots: {customRender: 'isAdmin'},
        },
        {
            title: '',
            dataIndex: 'isEnable',
            scopedSlots: {customRender: 'isEnable'},
        },
        {
            title: '',
            dataIndex: 'locked',
            scopedSlots: {customRender: 'locked'},
        },
        {
            title: '',
            dataIndex: 'lastLoginDate',
        },
        {
            title: '',
            dataIndex: 'loginTimes',
        },
        {
            title: '',
            dataIndex: 'createdDate',
        },
        {
            title: '',
            dataIndex: 'updatedDate',
        },
        {
            title: '',
            scopedSlots: {customRender: 'action'},
        },
    ];

    export default {
        data() {
            return {
                fieldsEquals: (rule, value, callback) => {
                    if (!value || value.trim() === '') {
                        callback();
                    }
                    if (this.form[rule.equalTo] !== value) {
                        callback(new Error(rule.message));
                    } else {
                        this.$refs.ruleForm.validateField(rule.equalTo);
                        callback();
                    }
                },
                data: [],
                columns,
                pagination: {
                    pageSizeOptions: ['10', '20', '50', '100'],
                    pageSize: 10,
                    showSizeChanger: true,
                    showTotal(total, range) {
                        return '' + total;
                    }
                },
                visible: false,
                form: {
                    id: null,
                    username: '',
					name:'',
                    password: '',
                    confirm_password: '',
                    isEnable: true,
                    isAdmin: false
                },
                rules: {}
            };
        },
        mounted() {
            this.fetch();
        },
        methods: {
            unlock(record){
                this.$http.put('/admin/user/unlock/'+record.username).then(response => {
                    this.$message.success("!");
                    this.fetch();
				});
			},
            handleSearch(values) {
                this.fetch({
                    pageSize: this.pagination.pageSize,
                    pageNum: 1,
                    ...values
                })
            },
            onAdd() {
                this.form = {
                    id: null,
                    username: '',
                    password: '',
                    confirm_password: '',
                    isEnable: true,
                    isAdmin: false
                };
                this.rules = {
                    username: [{required: true, message: '', trigger: 'blur'},],
                    name: [{required: true, message: '', trigger: 'blur'},],
                    password: [{required: true, message: '', trigger: 'blur'}, {
                        validator: this.fieldsEquals,
                        equalTo: 'confirm_password',
                        message: '',
                        trigger: 'change'
                    }],
                    confirm_password: [{required: true, message: '', trigger: 'blur'}, {
                        validator: this.fieldsEquals,
                        equalTo: 'password',
                        message: '',
                        trigger: 'change'
                    }]
                }
                this.showModal();
            },
            onEdit(record) {
                this.form = this.$mt.deepCopy(record);
                this.form.password = null;
                this.rules = {
                    username: [{required: true, message: '', trigger: 'blur'},],
                    name: [{required: true, message: '', trigger: 'blur'},],
                    password: [{
                        validator: this.fieldsEquals,
                        equalTo: 'confirm_password',
                        message: '',
                        trigger: 'change'
                    }],
                    confirm_password: [{
                        validator: this.fieldsEquals,
                        equalTo: 'password',
                        message: '',
                        trigger: 'change'
                    }]
                }
                this.showModal();
            },
            onDelete(id) {
                this.$http.delete('/admin/user/' + id).then(response => {
                    this.fetch();
                    this.$message.success("");
                });
            },
            handleTableChange(pagination, filters, sorter) {
                const pager = {...this.pagination};
                pager.current = pagination.current;
                this.pagination = pager;
                this.fetch({
                    pageSize: pagination.pageSize,
                    pageNum: pagination.current,
                    sortField: sorter.field,
                    sortOrder: sorter.order,
                    ...filters,
                });
            },
            fetch(params = {pageNum: 1, pageSize: 10}) {
                this.$http.get("/admin/user/page/list", {
                    params: params
                }).then(response => {
                    const pageBean = response.data.result;
                    const pagination = {...this.pagination};
                    pagination.total = pageBean.total;
                    pagination.pageSize = pageBean.pageSize;
                    this.data = pageBean.list;
                    this.pagination = pagination;
                })
            },
            showModal() {
                this.visible = true;
            },
            handleOk(e) {
                const data = this.$mt.deepClone(this.form);
                this.$refs.ruleForm.validate(valid => {
                    if (valid) {
                        if (!data.id) {
                            this.$http.post('/admin/user', data).then(response => {
                                this.$message.success('');
                                this.visible = false;
                                this.fetch();
                            });
                        } else {
                            this.$http.put('/admin/user', data).then(response => {
                                this.$message.success('');
                                this.visible = false;
                                this.fetch();
                            });
                        }
                    } else {
                        return false;
                    }
                });
            },
        },
        components: {
            SearchForm
        }
    };
</script>
<style scoped>

</style>