const direct_s = ['left', 'right']
const direct_1 = ['down', 'up', 'left', 'right']
const direct_1_b = ['downBig', 'upBig', 'leftBig', 'rightBig']
const direct_2 = ['topLeft', 'bottomRight', 'topRight', 'bottomLeft']
const direct_3 = ['downLeft', 'upRight', 'downRight', 'upLeft']

module.exports = [
  {name: 'back', alias: '', directions: direct_1},
  {name: 'bounce', alias: '', directions: direct_1.concat('default')},
  {name: 'fade', alias: '', directions: direct_1.concat(direct_1_b).concat(direct_2).concat('default')},
  {name: 'flip', alias: '', directions: ['x', 'y', 'default']},
  {name: 'lightSpeed', alias: '', directions: direct_s},
  {name: 'rotate', alias: '', directions: direct_3.concat('default')},
  {name: 'roll', alias: '', directions: ['default']},
  {name: 'zoom', alias: '', directions: direct_1.concat('default')},
  {name: 'slide', alias: '', directions: direct_1},
]
